:root {
  --clr-nutmeg: hsl(14, 45%, 36%);
  --clr-brandy-red: hsl(332, 51%, 32%);
  --clr-white: hsl(0, 0%, 100%);
  --clr-snow: hsl(330, 100%, 98%);
  --clr-eggshell: hsl(30, 54%, 90%);
  --clr-white-coffee: hsl(30, 18%, 87%);
  --clr-wenge-brown: hsl(30, 10%, 34%);
  --clr-dark-charcoal: hsl(24, 5%, 18%);
  --ff-sans: "Outfit", Arial, Helvetica, sans-serif;
  --ff-mono: "Source Code Pro", monospace;
  --ff-serif: "Young Serif", Cochin, Georgia, serif;
  --fs-xxlarge: 2.5rem;
  --fs-xlarge: 2.25rem;
  --fs-large: 1.75rem;
  --fs-med: 1.25rem;
  --fs-reg: 1rem;
  --fs-small: 0.6875rem;
  --gap: 1.25rem;
  --columns: 2;
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  font-style: normal;
  font-weight: normal;
}

body {
  font-family: var(--ff-sans);
  font-weight: 400;
  font-size: var(--fs-reg);
  line-height: 150%;
  position: relative;
}
html,
body {
  margin: 0px auto;
  width: 100%;
  box-sizing: border-box;
  background: var(--clr-eggshell);
  color: var(--clr-wenge-brown);
}

.visually-hidden {
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  height: 1px;
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
  width: 1px;
}

ul,
ol {
  list-style-position: inside;
  color: var(--clr-wenge-brown);
}

ul li,
ol li {
  margin-left: 0.5rem;
}
ul li:not(:last-child),
ol li:not(:last-child) {
  margin-bottom: 0.5rem;
}

ul li {
  list-style-type: none;
  padding-left: 2rem;
  background: transparent url(../images/bullet.svg) no-repeat left 50%;
}

ol {
  counter-reset: steps;
}
ol li {
  counter-increment: steps;
  list-style: none;
  padding: 0 0 0 2rem;
  position: relative;
}
ol li::before, ol li::after {
  content: "";
  left: 0;
  overflow: hidden;
  position: absolute;
  top: 0;
  width: 2rem;
}
ol li::before {
  content: counter(steps) ".";
  color: var(--clr-brandy-red);
  font-weight: 700;
  z-index: 1;
}

.attribution {
  font-size: var(--fs-small);
  text-align: center;
}

.attribution a {
  color: hsl(228, 45%, 44%);
}

/* prettier-ignore */
h1, h2 {
  font-weight: 400;
  line-height: 100%;
  margin: 0;
}

p {
  font-style: normal;
  line-height: 150%;
  font-weight: 400;
}

.recipe {
  background-color: var(--clr-white);
}
.recipe img {
  display: block;
  max-width: 100%;
  height: auto;
}
.recipe__body {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 2rem;
  padding: 2.5rem 2rem;
}
.recipe__header h1 {
  margin: 0;
  margin-bottom: 1.5rem;
  font-size: var(--fs-xlarge);
  font-family: var(--ff-serif);
  color: var(--clr-dark-charcoal);
}
.recipe__header p {
  font-weight: 400;
  margin: 0;
  padding: 0;
}
.recipe__prep {
  background-color: var(--clr-snow);
  padding: 1.5rem;
  border-radius: 0.75rem;
}
.recipe__prep h2 {
  font-size: var(--fs-med);
  color: var(--clr-brandy-red);
  font-weight: 600;
  line-height: 100%;
  margin: 0;
  padding: 0;
  margin-bottom: 1rem;
}
.recipe__ingredients, .recipe__instructions {
  padding-bottom: 2rem;
  border-bottom: 1px solid var(--clr-white-coffee);
}
.recipe__ingredients h2, .recipe__instructions h2, .recipe__nutrition h2 {
  font-family: var(--ff-serif);
  font-size: var(--fs-large);
  color: var(--clr-nutmeg);
  margin-bottom: 1.5rem;
}
.recipe__nutrition p {
  margin-bottom: 1.5rem;
}
.recipe__nutrition table {
  width: 100%;
  text-align: left;
  table-layout: fixed;
  vertical-align: top;
  border-collapse: collapse;
}
.recipe__nutrition table > :not(caption) > * > * {
  padding: 10px 20px;
}
.recipe__nutrition table tr:not(:last-child) {
  border-bottom: 1px solid var(--clr-white-coffee);
}
.recipe__nutrition table th {
  font-weight: 400;
}
.recipe__nutrition table td {
  font-weight: 700;
  color: var(--clr-brandy-red);
  width: 50%;
}
@media (min-width: 64em) {
  .recipe {
    max-width: 46rem;
    padding: 2.5rem;
    margin: 7.875rem auto 1.25rem;
    border-radius: 1.5rem;
  }
  .recipe img {
    border-radius: 0.75rem;
  }
  .recipe__body {
    padding: 2.5rem 0 0;
  }
  .recipe__header h1 {
    font-size: var(--fs-xxlarge);
  }
  .recipe__prep {
    padding: 1.75rem;
  }
}

.container {
  margin: 0 auto;
  width: 100%;
}
@media (min-width: 64em) {
  .container {
    max-width: 69.375rem;
  }
}